import React, {useCallback, useEffect, useState} from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Autocomplete from "@material-ui/lab/Autocomplete";
import CircularProgress from "@material-ui/core/CircularProgress";
import axios from 'axios';
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Background from "../bg.jpg";
import Tooltip from "@material-ui/core/Tooltip";
import Fab from "@material-ui/core/Fab";
import EditIcon from '@material-ui/icons/Edit';
import StarIcon from '@material-ui/icons/Star';
import CloseIcon from '@material-ui/icons/Close';
import AddIcon from '@material-ui/icons/Add';
import ShareIcon from '@material-ui/icons/Share';
import IconButton from "@material-ui/core/IconButton";

function Copyright() {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright Â© '}
            <Link color="inherit" href="https://material-ui.com/">
                Your Website
            </Link>{' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}


const useStyles = makeStyles(theme => ({
    '@global': {
        body: {
            backgroundColor: theme.palette.common.white,
        },
    },
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
    card: {
        minWidth: 275,
        position: 'relative'
    },
    bullet: {
        display: 'inline-block',
        margin: '0 2px',
        transform: 'scale(0.8)',
    },
    title: {
        fontSize: 14,
    },
    pos: {
        marginBottom: 12,
    },
    wrapper: {
        background: `url(${Background}) no-repeat`,
        backgroundSize: 'cover',
        overflow: 'hidden',
        height: '100vh',
        width: '100vw',
    },
    logo: {
        width: '30%',
        height: 'auto',
        boxShadow: '0 0 10px -1px #969696',
        marginBottom: '25px',
        border: '3px solid white',
        borderRadius: '8px'
    },
    fab: {
        margin: '0 10px',
    },
    closeButton: {
        position: 'absolute',
        right: '10px',
        top: '10px',
    },
    addIcon: {
        position: 'absolute',
        bottom: '20px',
        right: '20px',
    },
}));


export default function Search() {
    const [open, setOpen] = useState(false);
    const [options, setOptions] = useState([]);
    const [query, setQuery] = useState('');
    const [loading, setLoading] = useState(false);
    const [selectedPhrase, setSelectedPhrase] = useState(null);
    // const loading = open && options.length === 0;



    useEffect(() => {
        console.log('aaaaaaaa');
        setOptions([]);


        if (query) {
            setLoading(true);
            axios.get('http://localhost:8989/api/dictionary/search', {
                params: {
                    query: query
                }
            }).then((resp) => {
                console.log(resp);
                setOptions(resp.data);
                setLoading(false);
            });
        }


        return () => {
            setLoading(false);
        };
    }, [query]);


    const onSubmit = useCallback((event) => {
        event.preventDefault();
    }, []);

    const classes = useStyles();

    useEffect(() => {
        if (!open) {
            setOptions([]);
        }
    }, [open]);

    const filterOptions = (options, { inputValue }) => options;
    return (
        <div className={classes.wrapper}>
            <Container component="main" maxWidth="md">
                <CssBaseline />
                <div className={classes.paper}>
                    <img src={require('../logo.jpg')} alt="logo" className={classes.logo} />
                    <form className={classes.form} noValidate onSubmit={onSubmit}>
                        <Grid container>
                            <Grid item xs={12}>
                                <Autocomplete
                                    id="query"
                                    open={open}
                                    onOpen={() => {
                                        setOpen(true);
                                    }}
                                    onClose={() => {
                                        setOpen(false);
                                    }}
                                    onChange={(event, value) => {
                                        setSelectedPhrase(value);
                                        console.log('on Changeeeeeee', value);
                                    }}
                                    getOptionLabel={option => option.name}
                                    options={options}
                                    loading={loading}
                                    filterOptions={filterOptions}
                                    renderOption={(option) => {return <span>{option.name} <span style={{color: "gray"}}>({option.meaning})</span> / <span style={{color: "gray"}}>({option.meaning_fa})</span></span>}}
                                    renderInput={params => (
                                        <TextField
                                            {...params}
                                            label="Search anything..."
                                            value={query}
                                            fullWidth
                                            variant="outlined"
                                            onChange={(event) => {console.log('changeddddddd'); setQuery(event.target.value)}}
                                            InputProps={{
                                                ...params.InputProps,
                                                endAdornment: (
                                                    <React.Fragment>
                                                        {loading ? <CircularProgress color="inherit" size={20} /> : null}
                                                        {params.InputProps.endAdornment}
                                                    </React.Fragment>
                                                ),
                                            }}
                                        />
                                    )}
                                />
                                {selectedPhrase &&
                                <Card className={classes.card}>
                                    <br/>
                                    <CardContent>
                                        <IconButton color="primary" aria-label="upload picture" component="span" className={classes.closeButton}
                                        onClick={() => {
                                            setSelectedPhrase(null);
                                        }}>
                                            <CloseIcon />
                                        </IconButton>

                                        <Typography variant="h4" component="h2">
                                            {selectedPhrase.name}
                                        </Typography>
                                        <Typography color="textSecondary">
                                            <br/>
                                            {selectedPhrase.segments.map(segment => {
                                                return <span key={segment.id}>
                                                    {' {'}
                                                        {segment.content}
                                                        <span style={{fontSize: 'smaller', color: '#c9c9c9'}}>
                                                            {' (' + segment.description + ')'}
                                                        </span>
                                                    {'} '}
                                                </span>;
                                            })}
                                        </Typography>
                                        <Typography className={classes.pos} color="textSecondary" style={{fontSize: 'small', fontStyle: 'italic'}}>
                                            <br/>
                                            Example: {selectedPhrase.example}
                                        </Typography>
                                        <Typography variant="body2" component="p">
                                            <br/>
                                            {selectedPhrase.meaning}
                                            <br/>
                                            <br/>
                                            {selectedPhrase.meaning_fa}
                                            <br/>
                                            <br/>
                                        </Typography>
                                        <Tooltip title="Suggest an edit">
                                            <Fab aria-label="edit" className={classes.fab}>
                                                <EditIcon />
                                            </Fab>
                                        </Tooltip>
                                        <Tooltip title="Share the word">
                                            <Fab aria-label="share" className={classes.fab}>
                                                <ShareIcon />
                                            </Fab>
                                        </Tooltip>
                                        <Tooltip title="Add to favourites">
                                            <Fab aria-label="favorite" className={classes.fab}>
                                                <StarIcon color="primary"/>
                                            </Fab>
                                        </Tooltip>

                                    </CardContent>
                                </Card>
                                }
                            </Grid>
                        </Grid>
                    </form>
                </div>
                <Box mt={8}>
                    <Copyright />
                </Box>
                <Tooltip title="Suggest a new word">
                    <Fab aria-label="edit" className={classes.addIcon}>
                        <AddIcon />
                    </Fab>
                </Tooltip>
            </Container>
        </div>
    );
}